:root {
  --color-orange: #E87722;
  --color-yellow: #F5CD26;
  --color-black: #231F20;
  --color-text: #231F20;
  --color-white: #FFFFFF;
  --color-gray: #E5E5E5;
  --color-gray-light: #FAFAFA;
  --color-gray-light-1: #FAFAFA;
  --color-gray-light-2: #F7F7F7;
  --color-gray-light-3: #F2F2F2;
  --color-gray-light-4: #EAEAEA;
  --color-gray-darker: #999999;
  --color-gray-darker-to-white: #999999;
  --color-gray-darker-2: #848283;
  --color-blue: #0051CC;
  --color-blue-hover: #0065FF;
  --body-font-family: "Open Sans", sans-serif, ;
}

.slideshow-item {
  position: relative;
  background: #E7C7C7;
  opacity: 0.9;
  border: 2px solid var(--color-white);
}
.slideshow-item .info {
  position: absolute;
  padding: 30px 60px;
  bottom: 0;
  left: 0;
  color: #FFF;
}
.slideshow-item .info .title, .slideshow-item .info .title a {
  text-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
  font-weight: bold;
  font-family: "Source Sans Pro", sans-serif;
  color: var(--color-text);
  font-size: 26px;
  line-height: 28px;
  color: #FFF;
  margin-bottom: 10px;
}
.slideshow-item .info .description {
  text-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
}

.flipster--coverflow .flipster__item__content {
  -webkit-box-reflect: unset !important;
  box-reflect: unset !important;
}